.title {
    font-size: 60px;
    margin-bottom: 80px;
    text-align: center;
}

.content {
    display: flex;
    align-items: center;
    gap: 100px;
}

.imgContainer {
    flex: 1;
    height: 500px;
    position: relative;
}

.img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    animation: move 2.8s infinite ease alternate;
}

@keyframes move {
    0% {
        transform: translateY(-15px);
    }

    100% {
        transform: translateY(0px) scale(1.03);
    }
}

.form {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.input,
.textArea {
    padding: 18px;
    background-color: transparent;
    outline: none;
    border: 3px solid #bbb;
    color: #bbb;
    font-size: 18px;
    font-weight: bold;
}

.input:hover,
.textArea:hover {
    border: 3px solid #3dc07e;
}

.input:focus,
.textArea:focus {
    border: 3px solid #3dc07e;
}

.button {
    width: 150px;
    padding: 20px;
    cursor: pointer;
    background-color: #53C28B;
    border: none;
    border-radius: 5px;
    color: #eee;
    font-weight: bold;
}

.error {
    align-self: center;
    color: rgba(228, 97, 50, 0.803);
}

@media only screen and (max-width: 426px) {
    .container {
        margin-bottom: 2rem;
    }

    .imgContainer {
        display: none;
    }

    .title {
        margin-bottom: 1rem;
    }
}